// components/AuthMenu.tsx
'use client'

import Link from 'next/link'
import { useUser, SignInButton, SignUpButton, SignOutButton } from '@clerk/nextjs'
import {
  DropdownMenu,
  DropdownMenuTrigger,
  DropdownMenuContent,
  DropdownMenuItem,
} from '@/components/ui/dropdown-menu'
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar'

export function AuthMenu() {
  const { user, isLoaded, isSignedIn } = useUser()

  // Wait until Clerk finishes loading the user
  if (!isLoaded) return null

  return (
    <DropdownMenu>
      {/* The avatar that toggles the dropdown */}
      <DropdownMenuTrigger asChild>
        <Avatar>
          {isSignedIn && user ? (
            // Display the user's current profile image URL
            <AvatarImage
              src={user.profileImageUrl}
              alt={user.fullName ?? 'User'}
            />
          ) : (
            // Fallback avatar when not signed in (you can customize initials or icon here)
            <AvatarFallback>G</AvatarFallback>
          )}
        </Avatar>
      </DropdownMenuTrigger>

      <DropdownMenuContent align="end" className="w-48">
        {isSignedIn && user ? (
          <>
            {/* Signed‐in menu */}
            <div className="px-4 py-2 border-b border-gray-200">
              <p className="text-sm font-medium">
                {user.firstName ?? user.fullName}
              </p>
              <p className="text-xs text-gray-500">
                {user.primaryEmailAddress?.emailAddress}
              </p>
            </div>

            <DropdownMenuItem asChild>
              <Link href="/profile" className="w-full">
                Profile
              </Link>
            </DropdownMenuItem>

            <DropdownMenuItem asChild>
              <Link href="/dashboard" className="w-full">
                Dashboard
              </Link>
            </DropdownMenuItem>

            <DropdownMenuItem>
              <SignOutButton>
                <button className="w-full text-left">Sign out</button>
              </SignOutButton>
            </DropdownMenuItem>
          </>
        ) : (
          <>
            {/* Not signed‐in (show Sign In / Register buttons) */}
            <div className="flex flex-col space-y-2 px-4 py-3">
              <SignInButton>
                <button className="py-1 px-2 border border-gray-300 rounded text-sm text-gray-700 hover:bg-gray-100">
                  Sign in
                </button>
              </SignInButton>

              <SignUpButton>
                <button className="py-1 px-2 bg-blue-600 text-white rounded text-sm hover:bg-blue-700">
                  Register
                </button>
              </SignUpButton>
            </div>
          </>
        )}
      </DropdownMenuContent>
    </DropdownMenu>
  )
}
